.c-usps-2 {
  background-color: $s-color--background-impact;
  color: $s-color--font-invert;
  overflow: hidden;

  .o-link {
    color: $s-color--font-invert;

    &:before {
      background: $s-color--font-invert;
    }
  }

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner {
    padding: 4.875rem $s-gutter--edge-mobile min(7.9375rem, 34%);
    position: relative;
  }

  &__usps.o-usps {
    margin-bottom: 7.125rem;
  }

  &__image-wrapper {
    @include t-absolute-center-x();
    width: 100%;
    top: 81.5%;
    padding: 0 $s-gutter--edge-mobile;
    max-width: 25.3125rem;
  }

  &__image-1-container {
    width: 65%;
    padding-bottom: 83.2%;
    z-index: $s-z--text-overlay;
  }

  &__image-2-container {
    position: absolute;
    top: -3.6875rem;
    right: $s-gutter--edge-mobile;
    width: 48%;
    padding-bottom: 58.7%;
    z-index: $s-z--illustration-overlay;
  }

  &__illustration-background-1 {
    position: absolute;
    top: -3.5625rem;
    left: -1.0625rem;
    z-index: $s-z--reset;
  }

  &__illustration-background-2 {
    position: absolute;
    bottom: 0;
    right: 10%;
    z-index: $s-z--reset;
    width: 73%;
  }

  &__background-setter {
    width: 100%;
    height: 12.5rem;
    padding-bottom: min(32%, 15.5rem);
  }

  @include t-media-query($s-small-up) {

    &__inner {
      padding: 4.875rem $s-gutter--edge-mobile 5rem;
    }
  }

  @include t-media-query($s-medium-up) {

    &__inner {
      padding-top: 9.3125rem;
      padding-bottom: 19.375rem;
    }


    &__image-wrapper {
      top: 62.5%;
      max-width: 64%;
      padding: 0;
    }

    &__illustration-background-1 {
      top: -6.4375rem;
      left: -13%;
    }

    &__illustration-background-2 {
      bottom: unset;
      top: 8.4375rem;
      right: -8%;
      width: 30%;
    }

    .o-usps {
      &__cta-container {
        margin-bottom: 3.5625rem;
      }

      &__usp {
        align-items: center;
      }
    }

    &__background-setter {
      width: 100%;
      height: 12.5rem;
      padding-bottom: min(32%, 35.9375rem);
    }
  }

  @include t-media-query($s-large-up) {

    &__inner {
      padding-top: 9.3125rem;
      padding-bottom: 36.9375rem;
    }

    &__image-wrapper {
      max-width: 90%;
      top: 60%;
      padding: 0;
      left: 51%;
    }

    &__image-1-container {
      width: 49%;
      padding-bottom: 62.5%
    }

    &__image-2-container {
      right: 0;
      top: -9.6875rem;
      width: 45.7%;
      padding-bottom: 55.7%;
    }

    &__illustration-background-1 {
      top: -11.4375rem;
      left: -8.375rem;
      width: 50%;
    }
  }

  @include t-media-query($s-xlarge-up) {

    &__image-wrapper {
      max-width: 91.5rem;
    }
  }
}